:root {
  --primary-color: #2563eb;
  --secondary-color: #1e293b;
  --text-color: #1e293b;
  --background-color: #fff;
  --accent-color: #3b82f6;
  --border-color: #e5e7eb;
  --card-background: #fff;
  --text-secondary: #9ca3af;
}

[data-theme="dark"] {
  --primary-color: #60a5fa;
  --secondary-color: #e2e8f0;
  --text-color: #f3f4f6;
  --background-color: #1e1e34;
  --accent-color: #3b82f6;
  --border-color: #94a6c3;
  --card-background: #1f2937;
  --text-secondary: #9ca3af;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  background-color: var(--background-color);
  transition: background-color .3s;
}

body {
  color: var(--text-color);
  background-color: var(--background-color);
  min-height: 100vh;
  font-family: Inter, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  transition: background-color .3s, color .3s;
}

header {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  display: flex;
  position: relative;
}

@media screen and (width >= 1280px) {
  header .logo {
    z-index: 0;
  }
}

header nav ul {
  gap: 2rem;
  margin: 0;
  list-style: none;
  display: flex;
}

header nav ul a {
  color: var(--text-color);
  letter-spacing: .05em;
  font-size: 1.1rem;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

header nav ul a:hover {
  color: var(--primary-color);
}

.hero {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.hero .hero-content {
  grid-template-columns: 1fr;
  align-items: center;
  gap: 4rem;
  display: grid;
}

@media screen and (width >= 1280px) {
  .hero .hero-content {
    grid-template-columns: 1fr 1fr;
  }
}

.hero .hero-text h1 {
  margin-bottom: 2rem;
  font-size: 3.5rem;
  line-height: 1.2;
}

.hero .hero-text p {
  color: var(--text-secondary);
  margin-bottom: 2rem;
}

.hero .hero-text .cta-buttons {
  gap: 1rem;
  display: flex;
}

.hero .hero-text .cta-buttons .btn-primary {
  background-color: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border-radius: 8px;
  padding: .5rem 1rem;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.hero .hero-text .cta-buttons .btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px #0000001a;
}

.hero .hero-text .cta-buttons .btn-secondary {
  color: var(--text-color);
  border: 2px solid var(--text-color);
  cursor: pointer;
  background-color: #0000;
  border-radius: 8px;
  padding: .5rem 1rem;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.hero .hero-text .cta-buttons .btn-secondary:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px #0000001a;
}

.hero .hero-image {
  justify-content: center;
  align-items: center;
  display: flex;
}

.hero .hero-image img {
  object-fit: contain;
  border-radius: 100px;
  width: 100%;
  max-width: 500px;
  height: auto;
}

@media screen and (width >= 1280px) {
  .hero .hero-image {
    justify-content: flex-end;
  }
}

.latest-codes {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.latest-codes h2 {
  margin-bottom: 2rem;
  font-size: 2.5rem;
}

.latest-codes .projects-grid {
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
  display: grid;
}

.social-links {
  align-items: center;
  gap: 1rem;
  margin-left: auto;
  padding-right: 1rem;
  display: flex;
}

@media screen and (width <= 1280px) {
  .social-links {
    display: none;
  }
}

.social-links a, .social-links button {
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: transform .2s;
  display: flex;
}

.social-links a:hover, .social-links button:hover {
  transform: scale(1.1);
}

.social-links .social-icon {
  object-fit: contain;
  width: 24px;
  height: 24px;
}

[data-theme="dark"] .social-links a[href*="github"] .social-icon {
  filter: invert();
}

.social-links button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}

.mobile-social-links {
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding-top: 4rem;
  display: flex;
  position: absolute;
  bottom: 4rem;
  left: 0;
  right: 0;
}

.mobile-social-links a, .mobile-social-links button {
  color: var(--text-color);
  text-decoration: none;
  transition: color .3s;
}

.mobile-social-links a:hover, .mobile-social-links button:hover {
  color: var(--primary-color);
}

.mobile-social-links .social-icon {
  object-fit: contain;
  width: 24px;
  height: 24px;
}

[data-theme="dark"] .mobile-social-links a[href*="github"] .social-icon {
  filter: invert();
}

.mobile-social-links button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}

.project-card {
  background: var(--card-background);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  flex-direction: column;
  gap: 1rem;
  padding: 2rem;
  transition: all .3s;
  display: flex;
}

.project-card h3 {
  color: var(--text-color);
  align-items: center;
  gap: 1rem;
  margin: 0;
  font-size: 2rem;
  display: flex;
}

.project-card h3:before {
  content: "";
  background-image: url("data:image/svg+xml,<svg xmlns=\"http://www.w3.org/2000/svg\" viewBox=\"0 0 24 24\" fill=\"%23333\"><path d=\"M12 0c-6.626 0-12 5.373-12 12 0 5.302 3.438 9.8 8.207 11.387.599.111.793-.261.793-.577v-2.234c-3.338.726-4.033-1.416-4.033-1.416-.546-1.387-1.333-1.756-1.333-1.756-1.089-.745.083-.729.083-.729 1.205.084 1.839 1.237 1.839 1.237 1.07 1.834 2.807 1.304 3.492.997.107-.775.418-1.305.762-1.604-2.665-.305-5.467-1.334-5.467-5.931 0-1.311.469-2.381 1.236-3.221-.124-.303-.535-1.524.117-3.176 0 0 1.008-.322 3.301 1.23.957-.266 1.983-.399 3.003-.404 1.02.005 2.047.138 3.006.404 2.291-1.552 3.297-1.23 3.297-1.23.653 1.653.242 2.874.118 3.176.77.84 1.235 1.911 1.235 3.221 0 4.609-2.807 5.624-5.479 5.921.43.372.823 1.102.823 2.222v3.293c0 .319.192.694.801.576 4.765-1.589 8.199-6.086 8.199-11.386 0-6.627-5.373-12-12-12z\"/></svg>");
  background-repeat: no-repeat;
  background-size: contain;
  width: 24px;
  height: 24px;
  display: inline-block;
}

.project-card p {
  color: var(--text-secondary);
  margin: 0;
  font-size: .95rem;
  line-height: 1.5;
}

.project-card a {
  color: var(--text-color);
  align-items: center;
  gap: 1rem;
  margin-top: auto;
  font-weight: 500;
  text-decoration: none;
  display: flex;
}

.project-card a i {
  font-size: 1.2rem;
}

.mobile-menu {
  background-color: var(--background-color);
  z-index: 999;
  text-align: center;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100vh;
  padding: 4rem 2rem;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-100%);
}

.mobile-menu.active {
  transform: translateY(0);
}

.mobile-menu .mobile-menu-header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 8rem;
  padding-top: 2rem;
  display: flex;
  position: relative;
}

.mobile-menu .mobile-menu-header .logo {
  color: #fff;
  background-color: #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  margin: 4rem auto 0;
  font-size: 2.5rem;
  font-weight: bold;
  display: flex;
}

.mobile-menu .mobile-menu-header .close-menu {
  cursor: pointer;
  background: none;
  border: none;
  width: 40px;
  height: 40px;
  position: absolute;
  top: 2rem;
  right: 1rem;
}

.mobile-menu .mobile-menu-header .close-menu span {
  background-color: var(--text-color);
  width: 100%;
  height: 2px;
  position: absolute;
  left: 0;
}

.mobile-menu .mobile-menu-header .close-menu span:first-child {
  top: 50%;
  transform: rotate(45deg);
}

.mobile-menu .mobile-menu-header .close-menu span:last-child {
  top: 50%;
  transform: rotate(-45deg);
}

.mobile-menu .mobile-nav {
  margin-top: 4rem;
}

.mobile-menu .mobile-nav ul {
  flex-direction: column;
  gap: 4rem;
  padding: 0;
  list-style: none;
  display: flex;
}

.mobile-menu .mobile-nav li {
  margin: 0;
}

.mobile-menu .mobile-nav a {
  color: var(--text-color);
  letter-spacing: .05em;
  font-size: 2.5rem;
  font-weight: 500;
  text-decoration: none;
}

.mobile-menu .mobile-nav a:hover {
  color: var(--primary-color);
}

.mobile-menu .mobile-social-links {
  justify-content: center;
  gap: 2rem;
  padding-top: 4rem;
  display: flex;
  position: absolute;
  bottom: 4rem;
  left: 0;
  right: 0;
}

.mobile-menu .mobile-social-links a, .mobile-menu .mobile-social-links button {
  color: var(--text-color);
  font-size: 1.5rem;
  text-decoration: none;
  transition: color .3s;
}

.mobile-menu .mobile-social-links a:hover, .mobile-menu .mobile-social-links button:hover {
  color: var(--primary-color);
}

.mobile-menu .mobile-social-links button {
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}

@media screen and (width >= 1280px) {
  .mobile-menu {
    display: none;
  }
}

.menu-toggle {
  cursor: pointer;
  z-index: 1000;
  background: none;
  border: none;
  width: 40px;
  height: 40px;
  padding: 0;
  display: none;
  position: relative;
}

.menu-toggle span {
  background-color: var(--text-color);
  width: 100%;
  height: 2px;
  margin: 8px 0;
  transition: all .3s;
  display: block;
}

@media screen and (width <= 1280px) {
  .menu-toggle {
    display: block;
  }
}

.desktop-nav {
  margin-right: auto;
  padding-left: 1rem;
  display: flex;
}

@media screen and (width <= 1280px) {
  .desktop-nav {
    display: none;
  }
}

.desktop-nav ul {
  align-items: center;
  gap: 3rem;
  list-style: none;
  display: flex;
}

header .logo {
  color: #fff;
  z-index: 1;
  cursor: pointer;
  background-color: #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  font-size: 2.5rem;
  font-weight: bold;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (width >= 1280px) {
  header .logo {
    z-index: 0;
  }
}

header .logo:hover {
  transform: translateX(-50%)scale(1.1);
  box-shadow: 0 4px 12px #0003;
}

.mobile-menu.active ~ .menu-toggle {
  display: none;
}

@media screen and (width >= 1280px) {
  .menu-toggle, .mobile-menu {
    display: none !important;
  }
}

.project-card h3, .project-card a {
  color: var(--text-color);
}

.project-card a:hover {
  color: var(--primary-color);
}

footer {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

footer p {
  color: var(--text-secondary);
}

.about-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.about-section .about-content {
  grid-template-columns: 1fr;
  align-items: start;
  gap: 4rem;
  display: grid;
}

@media screen and (width >= 1280px) {
  .about-section .about-content {
    grid-template-columns: 2fr 1fr;
    align-items: flex-start;
  }
}

.about-section .about-text {
  order: 1;
}

.about-section .about-text h1 {
  color: var(--text-color);
  margin-bottom: 2rem;
  font-size: 3.5rem;
}

.about-section .about-text .lead {
  color: var(--text-secondary);
  margin-bottom: 2rem;
  font-size: 1.25rem;
}

.about-section .about-text p {
  margin-bottom: 2rem;
  line-height: 1.8;
}

.about-section .about-image {
  order: 2;
}

@media screen and (width <= 1280px) {
  .about-section .about-image {
    margin: 0 auto;
  }
}

.about-section .about-image img {
  border-radius: 8px;
  width: 100%;
  max-width: 400px;
  height: auto;
  margin: 0 auto;
  display: block;
  box-shadow: 0 4px 12px #0000001a;
}

@media screen and (width >= 1280px) {
  .about-section .about-image img {
    position: sticky;
    top: 4rem;
  }
}

.skills-section {
  clear: both;
  margin-top: 4rem;
  overflow: hidden;
}

.skills-section h2 {
  color: var(--text-color);
  margin-bottom: 3rem;
  font-size: 2.5rem;
}

.skills-section .skills-scroll-container {
  scrollbar-width: none;
  -ms-overflow-style: none;
  padding: 1rem 0;
  position: relative;
  overflow-x: auto;
}

.skills-section .skills-scroll-container::-webkit-scrollbar {
  display: none;
}

.skills-section .skills-scroll-container:before, .skills-section .skills-scroll-container:after {
  content: "";
  z-index: 2;
  pointer-events: none;
  width: 100px;
  position: absolute;
  top: 0;
  bottom: 0;
}

.skills-section .skills-scroll-container:before {
  background: linear-gradient(to right, var(--background-color), transparent);
  left: 0;
}

.skills-section .skills-scroll-container:after {
  background: linear-gradient(to left, var(--background-color), transparent);
  right: 0;
}

.skills-section .skills-scroll {
  gap: 2rem;
  min-width: max-content;
  padding: 2rem;
  animation: 30s linear infinite scrollSkills;
  display: flex;
}

.skills-section .skills-scroll:hover {
  animation-play-state: paused;
}

.skills-section .skill-item {
  background: var(--card-background);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  min-width: 150px;
  padding: 1.5rem;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.skills-section .skill-item:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 12px #0000001a;
}

.skills-section .skill-item:hover .skill-icon {
  transform: scale(1.1);
}

.skills-section .skill-item:hover .skill-progress:after {
  animation-play-state: running;
}

.skills-section .skill-item .skill-icon {
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  transition: transform .3s;
  display: flex;
}

.skills-section .skill-item .skill-icon img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  filter: var(--icon-filter, none);
}

.skills-section .skill-item .skill-info {
  flex-direction: column;
  align-items: center;
  gap: .5rem;
  width: 100%;
  display: flex;
}

.skills-section .skill-item .skill-name {
  color: var(--text-color);
  text-align: center;
  font-size: .9rem;
  font-weight: 500;
}

.skills-section .skill-item .skill-bar {
  background: var(--border-color);
  border-radius: 3px;
  width: 100%;
  height: 6px;
  position: relative;
  overflow: hidden;
}

.skills-section .skill-item .skill-bar .skill-progress {
  background: linear-gradient(90deg, var(--primary-color), var(--accent-color));
  border-radius: 3px;
  height: 100%;
  transition: width 1s ease-in-out;
  position: relative;
  overflow: hidden;
}

.skills-section .skill-item .skill-bar .skill-progress:after {
  content: "";
  background: linear-gradient(90deg, #ffffff1a 0%, #fff3 50%, #ffffff1a 100%);
  animation: 2s infinite paused shimmer;
  position: absolute;
  inset: 0;
}

@keyframes scrollSkills {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(calc(-50% - 1rem));
  }
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(100%);
  }
}

[data-theme="dark"] .skill-item .skill-icon img {
  --icon-filter: invert(1);
}

.experience-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.experience-section h2 {
  color: var(--text-color);
  margin-bottom: 4rem;
  font-size: 2.5rem;
}

.experience-section .experience-timeline {
  gap: 4rem;
  display: grid;
}

.experience-section .experience-item {
  background: var(--card-background);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 3rem;
  transition: all .3s;
}

.experience-section .experience-item:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 12px #0000001a;
}

.experience-section .experience-item h3 {
  color: var(--text-color);
  margin-bottom: 1rem;
  font-size: 2rem;
}

.experience-section .experience-item .company {
  color: var(--primary-color);
  margin-bottom: 1rem;
  font-weight: 500;
}

.experience-section .experience-item .period {
  color: var(--text-secondary);
  margin-bottom: 2rem;
  font-size: .9rem;
}

.experience-section .experience-item p {
  color: var(--text-secondary);
  line-height: 1.6;
}

.projects-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.projects-section .projects-header {
  text-align: center;
  margin-bottom: 6rem;
}

.projects-section .projects-header h1 {
  color: var(--text-color);
  margin-bottom: 2rem;
  font-size: 3.5rem;
}

.projects-section .projects-header .lead {
  color: var(--text-secondary);
  max-width: 800px;
  margin: 0 auto;
  font-size: 1.25rem;
}

.projects-section .projects-grid {
  grid-template-columns: 1fr;
  gap: 4rem;
  display: grid;
}

@media screen and (width >= 1280px) {
  .projects-section .projects-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.project-card {
  background: var(--card-background);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  transition: all .3s;
  overflow: hidden;
}

.project-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 12px #0000001a;
}

.project-card .project-image {
  width: 100%;
  height: 240px;
  overflow: hidden;
}

.project-card .project-image img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: transform .3s;
}

.project-card .project-image img:hover {
  transform: scale(1.05);
}

.project-card .project-content {
  padding: 2rem;
}

.project-card .project-content h3 {
  color: var(--text-color);
  margin-bottom: 1rem;
  font-size: 2rem;
}

.project-card .project-content .project-tech {
  color: var(--primary-color);
  margin-bottom: 2rem;
  font-size: .9rem;
}

.project-card .project-content .project-description {
  color: var(--text-secondary);
  margin-bottom: 2rem;
  line-height: 1.6;
}

.project-card .project-content .project-links {
  gap: 2rem;
  display: flex;
}

.project-card .project-content .project-links a {
  color: var(--text-color);
  align-items: center;
  gap: 1rem;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.project-card .project-content .project-links a:hover {
  color: var(--primary-color);
}

.project-card .project-content .project-links a .link-icon {
  width: 20px;
  height: 20px;
}

.snippets-section {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.snippets-section .snippets-header {
  text-align: center;
  margin-bottom: 6rem;
}

.snippets-section .snippets-header h1 {
  color: var(--text-color);
  margin-bottom: 2rem;
  font-size: 3.5rem;
}

.snippets-section .snippets-header .lead {
  color: var(--text-secondary);
  max-width: 800px;
  margin: 0 auto;
  font-size: 1.25rem;
}

.snippets-section .snippets-grid {
  grid-template-columns: 1fr;
  gap: 4rem;
  display: grid;
}

@media screen and (width >= 1280px) {
  .snippets-section .snippets-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

.snippet-card {
  background: var(--card-background);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  transition: all .3s;
  overflow: hidden;
}

.snippet-card:hover {
  transform: translateY(-4px);
